.btn{
    color:white;
    font-weight: bold;
    display: inline-block;
    padding: 1rem 4rem;
    border-radius: $button-radius;
    border: none;

    &__primary {
    background-color: $color-primary;
    box-shadow: 3px 10px 14px $color-primary-shadow;
    }

    &__redish {
        background-color: $color-redish;
    }

     &__green {
            background-color: $color-green;
        }
     &__large {
             padding: 22px 155px;
         }
    }